import React from "react";
import ReactDOM from "react-dom/client";
import {
  BrowserRouter,
  Routes,
  Route,
  Navigate,
  Outlet,
} from "react-router-dom";
import axios from "axios";
import loadable from "@loadable/component";
import { useNavigate } from "react-router-dom";
import EditContent from "./components/pages/editContent/EditContent";
import Contest from "./components/pages/category/Contest";
// import LayoutPage from './components/pages/shared/Layout';

const NotFound = loadable(() => import("./components/Notfound"));
const Login = loadable(() => import("./components/auth/Login"));
const ForgotPassword = loadable(() =>
  import("./components/auth/ForgotPassword")
);
const VerifyForgotPassword = loadable(() =>
  import("./components/auth/VerifyForgotOtp")
);
const ResetPassword = loadable(() => import("./components/auth/ResetPassword"));
const Dashboard = loadable(() => import("./components/pages/Dashboard"));
const Usermanager = loadable(() => import("./components/pages/User/list"));

// user management

//file management

const Filesection = loadable(() =>
  import("./components/pages/filedatamanagement/Addfile")
);

// Game section

const Game = loadable(() => import("./components/pages/game/list"));
const NewGame = loadable(() => import("./components/pages/game/addgame"));

// Sport category

const Categorylist = loadable(() => import("./components/pages/category/list"));
const Addcategory = loadable(() =>
  import("./components/pages/category/addCategory")
);
const Editcategory = loadable(() =>
  import("./components/pages/category/editCategory")
);
const Prediction = loadable(() =>
  import("./components/pages/category/Prediction")
);
const Content = loadable(() => import("./components/pages/category/Content"));
const Ranking = loadable(() => import("./components/pages/category/Ranking"));

// sport Championship

// const championship section

const Championshiplist = loadable(() =>
  import("./components/pages/championship/list")
);

const ChangePassword = loadable(() =>
  import("./components/auth/ChangePassword")
);

let navigate = null;

//  axios.defaults.baseURL = 'https://webmobrildemo.com/obediant/';
axios.defaults.baseURL = "http://44.195.125.80:10058/api/v1";
//    axios.defaults.baseURL = 'http://localhost:9700';

//  Request interceptor
axios.interceptors.request.use((request) => {
  let bearertoken = localStorage.getItem("angolosport_auth_back");
  // console.log("bbbbb-----",bearertoken)
  if (bearertoken !== null) {
    bearertoken = JSON.parse(bearertoken).token;
  }
  request.headers["Authorization"] = "Bearer " + bearertoken;
  return request;
});
//  response interceptor
axios.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    if (error.response) {
      if (error.response.status === 401) {
        localStorage.removeItem("angolosport_auth_back");
        navigate("/");
      }
    }
    return Promise.reject(error);
  }
);

const ProtectedRoute = () => {
  navigate = useNavigate();
  if (!localStorage.getItem("angolosport_auth_back")) {
    return <Navigate to="/" />;
  }
  return <Outlet />;
};

const PublicRoute = () => {
  if (localStorage.getItem("angolosport_auth_back")) {
    return <Navigate to="/dashboard" />;
  }
  return <Outlet />;
};

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <BrowserRouter>
    <Routes>
      <Route element={<PublicRoute />}>
        <Route path="/" element={<Login />} />
        <Route path="/forgot-password" element={<ForgotPassword />} />
        <Route path="/forgot-password/otp" element={<VerifyForgotPassword />} />
        <Route path="/reset-password" element={<ResetPassword />} />
      </Route>
      <Route element={<ProtectedRoute />}>
        <Route path="/dashboard" element={<Dashboard />} />
        <Route path="/change-password" element={<ChangePassword />} />

        {/* user path  */}
        <Route path="/user" element={<Usermanager />} />
        {/* End user path  */}

        {/* use file   */}
        <Route path="/file" element={<Filesection />} />
        {/* End file  */}

        {/* Game path  */}
        <Route path="/game" element={<Game />} />
        <Route path="/game/addgame" element={<NewGame />} />
        {/* End Game Path  */}

        {/* category path  */}
        <Route path="/category" element={<Categorylist />} />
        <Route path="/category/addcategory" element={<Addcategory />} />
        <Route path="/category/editcategory/:id" element={<Editcategory />} />
        <Route path="/category/prediction/:id" element={<Prediction />} />
        <Route path="/category/ranking/:id" element={<Ranking />} />
        <Route path="/category/contest/:id" element={<Contest />} />

        <Route path="/content" element={<Content />} />
        <Route path="/content/edit/:id" element={<EditContent />} />

        {/* End category path  */}

        {/* Championship path  */}
        <Route path="/Championship" element={<Championshiplist />} />
        {/* End Championship path  */}
      </Route>
      <Route path="*" element={<NotFound />} />
    </Routes>
  </BrowserRouter>
);

