import React, { useEffect, useState } from "react";

import JoditEditor from "jodit-react";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import styled from "styled-components";

const EditContent = () => {
  const [content, setContent] = useState("");
  const [title, setTitle] = useState("");
  const params = useParams();
  const [newContent, setNewContent] = useState({});

  const navigate = useNavigate();

  useEffect(() => {
    axios
      .get(`/content/admin/${params.id}`)
      .then((res) => res.data.data)
      .then((data) => {
        setContent(data.pageContent);
        setTitle(data.pageHeading);
      });
  });

  const newContentData = {
    pageContent: newContent,
  };

  const updateContent = () => {
    console.log("id", newContentData);
    console.log("new content ==> ", newContentData);
    axios.patch(`/content/${params.id}`, newContentData).then((res) => {
      console.log("res ==> ", res);
    });
  };

  const handleCloseButton = () => {
    navigate("/content");
  };

  return (
    <Container>
      <h3 className="header">Edit Details</h3>
      <i className="fa fa-close fonts" onClick={handleCloseButton}></i>

      <div className="main">
        <div className="title"> {title}</div>
        <JoditEditor
          className="jodit-editor"
          // ref={editor}
          value={content}
          // config={config}
          // onBlur={updateContent}
          onChange={(newContent) => {
            setNewContent(newContent);
          }}
        />
        <button onClick={updateContent} className="Submit">
          Submit
        </button>
      </div>
    </Container>
  );
};

export default EditContent;

const Container = styled.div`
  width: 100%;
  min-height: 100vh;
  height: fit-content;
  display: flex;
  background: #fff;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 10px 5px;
  box-sizing: border-box;

  i {
    /* text-align: end; */
    position: absolute;
    top: 20px;
    right: 30px;
    font-size: 1.6rem;
    cursor: pointer;

    :hover {
      color: red;
      font-size: 1.8rem;
      cursor: pointer;
    }
  }

  .header {
    /* text-align: center; */
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 0;
    padding-left: 20px;
    color: gray;
  }

  .main {
    width: 60%;
    margin-bottom: 0;
    display: flex;
    justify-self: right;
    align-self: center;
    flex-direction: column;
    

    .title {
      height: fit-content;
      border: 1px solid gray;
      background-color: #e0dcdc;
      padding: 5px;
      margin-bottom: 1.2rem;
    }
    .jodit-editor {
      width: 95%;
      margin-bottom: 0.7rem;
    }

    button {
      padding: 8px 0;
      border: none;
      font-size: 1.2rem;

      :hover {
        color: blue;
        background-color: #969abd;
      }
    }
  }
`;
